/* CoreServiceSection.css */

.core-service-section {
    padding: 100px 20px;
    background: linear-gradient(135deg, #ffffff, #f3f9ff);
    text-align: center;

}

.core-container {
    max-width: 1200px;
    margin: 0 auto;
    margin-left: 20%;
    margin-right: 20%;
}

.section-header {
    margin-bottom: 60px;
}

.core-title {
    font-size: 0.85rem;
    color: #00a676;
    text-transform: uppercase;
    margin-bottom: 10px;
}



.core-services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 40px;
}

.core-service-card {
    background: #fff;
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.core-service-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.2);
}

.card-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.core-service-illustration {
    width: 80px;
    height: auto;
    border-radius: 50%;
    background-color: #e8f5ff;
    padding: 20px;
    margin-bottom: 15px;
}

.core-service-title {
    font-size: 1.8rem;
    color: #222;
    font-weight: bold;
}

.card-body {
    text-align: center;
}

.core-service-description {
    font-size: 1rem;
    color: #555;
    line-height: 1.6;
}




.coresection-title {
    font-size: 3rem;
    color: #222;
    font-weight: bold;
    margin-bottom: 20px;
    line-height: 1.2;
    text-align: center;
}

.coresection-gradient-text {
    background: linear-gradient(90deg, #045e6d, #00a676, #fcb081);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.coresection-description {
    font-size: 1.2em;
    margin: 15px 0;
    color: #444;
    margin-bottom: 50px;
}


@media (max-width: 768px) {
    .coresection-title {
        font-size: 2.5em;
    }

    .core-services-grid {
        grid-template-columns: 1fr;
    }

    .core-container {
        margin-left: 5%;
        margin-right: 5%;
    }
}