/* MarketplaceSection.css */

.marketplace-section {
    padding: 80px 20px;
    background: linear-gradient(135deg, #f3f4f6, #e8eff5);
}

.marketplace-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 40px;
}

.marketplace-content {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    align-items: center;
    justify-content: space-between;
}

.text-content {
    flex: 1;
    text-align: left;
    max-width: 800px;
}

.marketplace-title {
    font-size: 3rem;
    color: #222;
    font-weight: bold;
    margin-bottom: 20px;
    line-height: 1.2;
}

.marketplace-description {
    font-size: 1.2rem;
    color: #555;
    line-height: 1.8;
    margin-bottom: 30px;
}

.illustration-content {
    flex: 1;
    text-align: center;
}

.illustration-content img {
    width: 100%;
    max-width: 500px;
    height: auto;
    border-radius: 12px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
}

.marketplace-illustration {
    max-width: 100%;
    height: auto;
    border-radius: 15px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    .marketplace-container {
        flex-direction: column;
        gap: 20px;
    }

    .marketplace-content {
        flex-direction: column;
        text-align: center;
    }

    .text-content {
        max-width: 100%;
        margin-bottom: 20px;
    }

    .marketplace-title {
        font-size: 2.5rem;
    }


    .marketplace-description {
        font-size: 1rem;
    }

    .illustration-content {
        max-width: 100%;
    }

    .marketplace-illustration {
        border-radius: 12px;
    }
}